import React, { Component } from 'react';
import User from '../../../assets/img_avatar.png';
import '../style.css';

export default class index extends Component {

    state = { isClicked: false, text: '' };

    submitHandler(e) {
        e.preventDefault();
        const { list, profile } = this.props;
        const { text } = this.state;
        if (list && list.length > 0) {
            var tempList = list;
            const result = tempList.filter(i => i.name === e.target[1].value);
            if (result && result.length > 0 && result[0]._id) {
                var data = {
                    topicId: result[0]._id, postedBy: profile._id, feed: text
                }
                this.setState({ isClicked: false });
                this.props.submit(data);
            }
        }
    }

    renderOptions() {
        const { list } = this.props;
        return list && list.length > 0 && list.map(i => <option value={i.name} key={i.name} data-key={i.name} />);
    }

    render() {
        const { profile } = this.props;
        const { isClicked, text } = this.state;
        return <form className="col-12 p-0" style={{ display: 'flex', flexDirection: 'column', marginBottom: '30px', backgroundColor: '#ecf0f1', alignItems: 'center' }}
            onSubmit={(e) => this.submitHandler(e)}>
            <div className="col-12 pt-3 pb-2 pl-4 pr-4" style={{ borderBottom: '1px solid #dcdde1' }}>
                <h6 style={{ fontWeight: '600', color: 'grey' }}>Ask Somthing</h6>
            </div>
            {!isClicked && <div className="col-12 p-4" style={{ display: 'flex', flexDirection: 'row', alignItems: 'center', cursor: 'pointer' }}
                onClick={(e) => this.setState({ isClicked: true })}>
                <img src={profile.image ? profile.image : User} alt="user" style={{ width: '40px', height: '40px', borderRadius: '1000px' }} />
                <h6 className="ml-3" style={{ fontWeight: '600', fontSize: '16px', color: 'grey' }}>Hi {profile.name} ! What is on your mind?</h6>
            </div>}
            {isClicked && <div className="col-12 p-4" style={{ display: 'flex', flexDirection: 'column' }}>
                <textarea className="mb-3 post-comm" placeholder="Write somthing here !" id="text" value={text} onChange={e => this.setState({ text: e.target.value })} autoFocus />
                <div className="col-12 p-0" style={{ display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
                    <input type="text" list="topics" placeholder="eg. topic" className="topic-comm" />
                    <datalist id="topics">
                        {this.renderOptions()}
                    </datalist>
                    <button style={{ fontSize: '18px', marginLeft: '12px', color: 'grey', marginTop: '6px' }}
                        className="btn" type='submit'><span className="fa fa-send" /></button>
                </div>
            </div>}
        </form>
    }
}